import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import * as CryptoJS from 'crypto-js';
import { AuthenticationService, ToastService } from './../services';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  public form: FormGroup;
  public loading = false;
  public submitted = false;
  public isAuthenticated = true;

  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private authenticationService: AuthenticationService,
    private toastService: ToastService
  ) {}

  public ngOnInit() {
    this.form = this.formBuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

  public get loginForm() {
    return this.form.controls;
  }

  public onSubmit() {
    this.submitted = true;

    if (this.form.invalid) {
      return;
    }

    this.loading = true;
    const encrptPass = this.encrypt();

    this.authenticationService
      .login(this.loginForm.username.value, encrptPass)
      .pipe(first())
      .subscribe({
        next: () => {
          this.loading = false;
          this.isAuthenticated =
            this.authenticationService.userValue &&
            this.authenticationService.userValue.userRole
              ? true
              : false;
          if (this.isAuthenticated) {
            const returnUrl =
              this.route.snapshot.queryParams['returnUrl'] || '/';
            this.router.navigateByUrl(returnUrl);
          } else {
            this.authenticationService.logout();
          }
        },
        error: error => {
          this.toastService.showErrorToast(`User ID or password was wrong`);
          this.loading = false;
        }
      });
  }

  public encrypt() {
    const key = CryptoJS.enc.Utf8.parse('4512631236589784');
    const iv = CryptoJS.enc.Utf8.parse('4512631236589784');
    const encrypted = CryptoJS.AES.encrypt(
      CryptoJS.enc.Utf8.parse(this.loginForm.password.value.toString()),
      key,
      {
        keySize: 128 / 8,
        iv: iv,
        mode: CryptoJS.mode.CBC,
        padding: CryptoJS.pad.Pkcs7
      }
    ).toString();
    return encrypted;
  }
}
